import { Layout,Col,Row,Button,Divider,} from 'antd';

const {Content} = Layout;

export default function OrderDetails ({orderCardDescription}) {
    const {id,paymentMethod,
        address,client,clientNumber,
        comments,date,order,partner,
        personsCount,promotion,
        shipTo,total,time} = orderCardDescription
    console.log('id',id)
    return (
        <>
         <Content
            className="site-layout-background white padding-10"  
        >
            <Row justify='space-between' >
                    <Col>
                        <Row gutter={20}>
                            <Row></Row>
                            <Col className='font-weight-400'>{id}</Col>
                        <Button size="small" ></Button>
                    </Row>
                    <Row justify='space-between'><Col>: {date}</Col><span className='vertical-divider'> | </span><Col>: {time}</Col></Row>
                </Col>
                <Col><Row justify='space-between' gutter={10}><Button size="small"  className="turquoise margin-right-10 text-align-center" style={{backgroundColor:"rgba(64,220,208, 1)"}}> </Button> 
                <Button size="small" danger > </Button></Row>
                </Col>
            </Row>
            <Divider style={{marginTop: "5px",marginBottom:"5px"}}/>
            <Row >
                <Col  span={9} className='order-main-info'>
                    <div className='font-weight-600'>
                       
                    </div>
                    <Row className='light-blue-text'> </Row>
                    <Row>{client}</Row>
                    <Divider style={{margin: "0"}}/>
                    <Row className='light-blue-text'></Row>
                    <Row>{clientNumber}</Row>
                    <Divider style={{margin: "0"}}/>
                    <Row className='light-blue-text'></Row>
                    <Row>{address}</Row>
                    <Divider style={{margin: "0"}}/>
                    <Row className='light-blue-text'></Row>
                    <Row>{partner}</Row>
                    <Divider style={{margin: "0"}}/>
                    <Row className='light-blue-text'> </Row>
                    <Row>{shipTo}</Row>
                    <Row className='light-blue-text'>- </Row>
                    <Row >{personsCount}</Row>
                    <Divider style={{margin: "0"}}/>
                    <Row className='light-blue-text'> </Row>
                    <Row>{paymentMethod}</Row>
                    <Divider style={{margin: "0"}}/>
                    <Row className='light-blue-text'></Row>
                    <Row>{promotion}</Row>
                    <Divider style={{margin: "0"}}/>
                    <Row className='light-blue-text'> / </Row>
                    <Row>{total}</Row>
                </Col>
                <Col span={1}><Divider type='vertical' /></Col>
                <Col span={14} className='order-main-info'><span className='font-weight-600'>  </span>

                {order.map(ord => <div>
                    <Row>{ord.name}-<span>{ord.price} </span> <span> ({ord.count} .)</span></Row>
                    
                <Row></Row>
                {ord.additionals.map(add => (<Row>{add.name} - <span>{add.price}</span></Row>))}
                <Divider/>
                </div>)}

                <Col>
                <Row style={{marginTop:"100px"}} className='font-weight-600' >  </Row>
                <Row>{comments}</Row>
                </Col>
                </Col>
            </Row>
        </Content>
        </>
    )
}